@use "global" as *;


.wp-block-list {

}
/* : ul */
ul.wp-block-list {
    list-style: none; /*  */
    padding-left: 0;
}

ul.wp-block-list li {
    position: relative;  /*  */
    padding-left: 30px;  /*  */
}

/* li */
ul.wp-block-list li::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: rem(18);  /*  */
    height: 20px; /*  */
    background-image: url('../../assets/images/blog/starfish2.jpg');
    background-size: contain; /*  */
    background-repeat: no-repeat;
}
